import './home.css'
import React, { useEffect, useState } from 'react'
import { io } from "socket.io-client";
import CentralArea from '../components/CentralArea'
import ButtonModel from '../components/ButtonModel'
import InputText from '../components/InputText'
import QRCodeComp from "react-qr-code";
import { Ring, Waveform } from '@uiball/loaders'
import InputPassword from '../components/InputPassword'
import Modal from '../components/Modal'
export default function Home() {
  const [backPort] = useState(new URL(window.location.href).port)
  const socket = io(`http://localhost:${backPort}`);
  const [connection, setConnection] = useState('disconnected')
  const [modalState, setModalState] = useState(false)
  const [keyState, setKey] = useState(null)
  const [orgState, setOrg] = useState(null)
  useEffect(() => {
    fetch(`http://localhost:${backPort}/status`)
      .then(response => {
        if (!response.ok) {
          throw new Error('Erro ao obter dados da API');
        }
        return response.json();
      })
      .then(data => {
        !data ? setConnection('disconnected') : setConnection(data)

      })
      .catch(error => {
        console.error(error);
      });
  }, []);

  socket?.on('disconnect', () => {
    window.location.reload()
  })
  socket?.on('statusConnection', (status) => {
    setConnection(status)
  })
  function connect() {
    fetch(`http://localhost:${backPort}/connect`, {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json'
      },
      body: JSON.stringify({
        organization: orgState,
        apiKey: keyState
      })
    })
  }
  function disconnect() {
    fetch(`http://localhost:${backPort}/disconnect`, {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json'
      },
      body: JSON.stringify({
        organization: orgState,
        apiKey: keyState
      })
    })
  }
  function stateConnection(status) {
    if (status === 'connected') {
      return <Connected />
    } else if (!status || status === 'disconnected') {
      return <Disconnected />
    } else {
      return <QRcode qr={status} />
    }

  }

  function buttonState(status) {
    if (status === 'connected') {
      return 'Desconectar'
    } else if (!status || status === 'disconnected') {
      return 'Conectar'
    } else {
      return <Waveform
        size={30}
        lineWeight={5}
        speed={2}
        color="white"
      />
    }

  }
  return (
    <div className='home-container'>
      <CentralArea>
        <Modal status={modalState} onClose={() => { setModalState(false) }}>
          <div className="content-modal" style={{ display: 'flex', flexDirection: 'column', alignItems: 'center', textAlign: 'center' }}>
            <div style={{ padding: '.5rem' }}>Incentive este desenvolvedor independente.</div>
            <div className="qrcodepix">
              <img alt="pix" src="data:image/png;base64," />
            </div>
            <div className="description">
              <h3 style={{ margin: '0' }}>Chave Pix</h3>
              <div style={{ padding: '.5rem' }}>
                E-mail: pagamentos@izaiasferreira.com.br
              </div>
            </div>
          </div>
        </Modal>
        <div className="header">
          <div className="images">
            <img src="https://uxwing.com/wp-content/themes/uxwing/download/brands-and-social-media/chatgpt-icon.png" />
            <span>+</span>
            <img src="https://cantinhodabrantes.com.br/wp-content/uploads/2017/08/whatsapp-logo-PNG-Transparent.png" />
          </div>
          <div className="title">
            <h1>ChatGPT e Whatsapp</h1>
          </div>
        </div>



        <div className="body">
          <div className="form">
            <InputText size='large' placeholder='Cole/digite aqui o cdigo da sua organizao' onChange={(value) => {
              setOrg(value)
            }} />
            <InputPassword size='large' placeholder='Cole/digite aqui a a chave de autenticao' onChange={(value) => {
              setKey(value)
            }} />
          </div>
          <div className="status">
            {connection ? stateConnection(connection) : null}
          </div>
          <ButtonModel disabled={connection !== 'connected' || connection !== 'disconnected' ? false : true} onClick={() => {
            if (connection !== 'connected' && !keyState && !orgState || [keyState, orgState].includes('')) {
              alert('Preencha todos os campos')
            } else {
              if (connection !== 'connected') {
                connect()
              } else {
                disconnect()
              }
            }

          }}>{connection ? buttonState(connection) : 'Conectar'}</ButtonModel>
        </div>



        <div className="footer">
          <span>
            Desenvolvido por <a href="https://www.instagram.com/izaias.sferreira/">Izaias Ferreira</a>
          </span>
          <br />
          <span>
            Gostaria de fazer uma doao e incentivar o desenvolvimento? <span style={{ cursor: 'pointer', fontWeight: '600', color: 'var(--tree-color)' }} onClick={() => {
              setModalState(true)
            }}>CLIQUE AQUI</span>
          </span>
          <br/>
          <span>
            No sabe como conectar? <a href="https://github.com/izaiasferreira/chatGPT-Whatsapp/blob/9af93d6b1510b18a15390fe2021c6e629149e804/README.md">VEJA AQUI</a>
          </span>
        </div>
      </CentralArea >
    </div >
  )


}

function Connected() {
  return (
    <div className="connected-container" style={{ display: 'flex', flexDirection: 'column', alignItems: 'center', padding: '1rem' }}>
      <h1 style={{ color: 'var(--tree-color)' }}>Conectado</h1>
      <lord-icon
        src="https://cdn.lordicon.com/tqywkdcz.json"
        trigger="loop"
        delay="2000"
        colors="primary:#16c79e,secondary:#3080e8,tertiary:#0a5c49,quaternary:#66ee78"
        style={{ width: '250px', height: '250px' }}>
      </lord-icon>
    </div>
  )
}

function Disconnected() {
  return (
    <div className="connected-container" style={{ display: 'flex', flexDirection: 'column', alignItems: 'center', padding: '1rem' }}>
      <h1 style={{ color: 'var(--danger-color)' }}>Desconectado</h1>
      <lord-icon
        src="https://cdn.lordicon.com/wwneckwc.json"
        trigger="loop"
        delay="2000"
        colors="primary:#911710,secondary:#e83a30"
        style={{ width: '250px', height: '250px' }}>
      </lord-icon>
    </div>
  )
}

function QRcode({ qr }) {
  if (qr) {
    return (
      <div className='qrcode-container'>
        <div className='qrcode-content'>
          <span className='title'> <strong>Como se conectar:</strong></span>
          <span className='text-steps'>1 - Abra o Whatsapp {'>'} Configuraes {'>'} Aparelhos Conectados; <br />
            2 - Clique em Conectar dispositivo; <br />
            3 - Leia o QRCode abaixo.
          </span>
          <div className="qrcode-image">
            <span className='logo-qrcode'> <img src="./img/simbol-outline.png" alt="" /></span>
            <QRCodeComp value={qr.padEnd(220)} bgColor='#FFFFFF' fgColor='#000000' />
          </div>
          <span className='text-min'></span>
          <span className='text-min'>Caso a conexo no seja bem sucedida, tente de 3 a 5 vezes uma nova reconexo antes de <a href="https://wa.me/5586995726999"> entrar em contato com o suporte.</a></span>
        </div>
      </div>
    )
  }
  if (!qr) {
    return (
      <div className='qrcode-container'>
        <div className='qrcode-content'>
          <div className="qr-code-default">
            <span><Ring
              size={60}
              lineWeight={5}
              speed={2}
              color="blue"
            /></span>
          </div>
        </div>
      </div>
    )
  }
}